<script>
  import { _ } from 'svelte-i18n';
  import draft from 'stores/Draft';
  import * as draftUtil from 'util/DraftUtil';
  import YarnFillTable from 'components/YarnFillTable/YarnFillTable.svelte';

  let yarnFillTable;
  let useWarp;
  let useWeft;

  function apply() {
    let threads = yarnFillTable.getThreads();
    if (useWarp || useWeft) {
      draft.update((temp) => {
        if (useWeft) {
          draftUtil.applyColor(temp, threads, 'weft');
        }
        if (useWarp) {
          draftUtil.applyColor(temp, threads, 'warp');
        }
      });
    }
  }
</script>

<div>
  <YarnFillTable bind:this={yarnFillTable} />
  <div class="checkbox">
    <input type="checkbox" id="warp" name="warp" bind:checked={useWarp} />
    <label for="warp">Apply on warp</label>
  </div>
  <div class="checkbox">
    <input type="checkbox" id="weft" name="weft" bind:checked={useWeft} />
    <label for="weft">Apply on weft</label>
  </div>
</div>

<button on:click={apply}>{$_('page.pattern_fill.apply')}</button>

<style>
  .checkbox {
    display: flex;
  }
</style>
